import { ReactNode } from 'react';
import NextLink from 'next/link';

const Link = ({
  children,
  href,
  button = false,
}: {
  children: ReactNode;
  href: string;
  button?: boolean;
}) => (
  <button role={'link'} sty>
    <NextLink href={href}>
      {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
      <a>{children}</a>
    </NextLink>
  </button>
);

export default Link;
